export function mapEquals(a, b) {
    return isSubset(a, b) && isSubset(b, a);
}
export function map(map, f) {
    let retVal = new Map();
    map.forEach((value, key) => {
        retVal.set(key, f(value));
    });
    return retVal;
}
export function filter(map, predicate) {
    let retVal = new Map();
    map.forEach((value, key) => {
        if (predicate(value)) {
            retVal.set(key, value);
        }
    });
    return retVal;
}
export function difference(a, b) {
    const allKeys = new Set([...a.keys(), ...b.keys()]);
    let map = new Map();
    for (let key of allKeys.values()) {
        map.set(key, (a.get(key) || 0) - (b.get(key) || 0));
    }
    return map; //filter(map, v => v !== 0)
}
export function toString(map) {
    return `{ ${[...map.entries()].map(([k, v]) => `${k}: ${v}`).join(', ')} }`;
}
// plus versus sum
export function merge(maps) {
    let retVal = new Map();
    maps.forEach(map => {
        map.forEach((value, key) => {
            retVal.set(key, value + (retVal.get(key) || 0));
        });
    });
    return retVal;
}
export function intersection(a, b) {
    const allKeys = new Set([...a.keys(), ...b.keys()]);
    let map = new Map();
    for (let key of allKeys.values()) {
        if (a.has(key) && b.has(key)) {
            map.set(key, Math.min(a.get(key), b.get(key)));
        }
    }
    return map; //filter(map, v => v !== 0)
}
// a = {'brett': 20, 'nick': 30}
// b = {'brett': 20, 'nick': 30, 'cal': 40}
function isSubset(a, b) {
    for (let key of a.keys()) {
        if (!b.has(key) || a.get(key) !== b.get(key))
            return false;
    }
    return true;
}
