import { difference, filter, intersection, merge } from './MapLib';
import { multiplyCounts, singleton, sumAll, intersection as intersection2, difference as difference2 } from './Multiset';
export function compound(molecules, subscript = 1) {
    return {
        kind: 'Compound',
        molecules,
        subscript: subscript
    };
}
export function element(element, subscript = 1) {
    return {
        kind: 'Element',
        element,
        subscript: subscript
    };
}
// O2
const oxygen = element('O', 2);
const water = compound([element('H', 2), element('O')]);
function H_2O() {
    return compound([element('H', 2), element('O')]);
}
// ? H_2 + ? O_2 -->  ? H_2O
// 1 H_2 + 1 O_2 ---> 1 H_2O
// ...
// 2 H_2 + 1 O_2 --> 2 H_2O
export function equationWithCoefficient1(reactants, products) {
    return {
        // [number, Molecule][] = Molecule[]    <====> [number, Molecule] = Molecule
        reactants: reactants.map(reactant => [1, reactant]),
        products: products.map(product => [1, product])
    };
}
export function isBalanced(equation) {
    const categories = categorize(equation);
    return categories.owedInReactants.size === 0 && categories.owedInProducts.size === 0;
}
// map (on Array) :::: map :: (a -> b) -> (List)<a>   -> (List)<b>
// map (on Map<K, V>)  map :: (a -> b) -> (Map<K>)<a> -> (Map<K>)<b>
// map (on Maybe)      map :: (a -> b) -> (Maybe)<a>  -> (Maybe)<b>
// map :: (a -> b) -> F<a> -> F<b>
// 2 * H20 = 2 * {H: 2, O: 1} => {H: 2 * 2, O: 1 * 2}
// H: 4
// O: 2
export function countElements(molecule, coefficient = 1) {
    // switch(molecule.kind) {
    //     case "Element":
    //         return map(new Map([[molecule.element, molecule.subscript]]), v => v * coefficient)
    //     case "Compound":
    //         return map(
    //             merge(molecule.molecules.map(innerMolecule => countElements(innerMolecule))),
    //             v => v * molecule.subscript * coefficient
    //         );
    // }
    return countElements2(molecule, coefficient).elements;
}
export function countElements2(molecule, coefficient = 1) {
    switch (molecule.kind) {
        case "Element":
            return singleton(molecule.element, molecule.subscript * coefficient);
        case "Compound":
            return multiplyCounts(molecule.subscript * coefficient, sumAll(molecule.molecules.map(x => countElements2(x))));
    }
}
export function categorize(equation) {
    const reactants = merge(equation.reactants.map(([coefficient, molecule]) => countElements(molecule, coefficient)));
    const products = merge(equation.products.map(([coefficient, molecule]) => countElements(molecule, coefficient)));
    const positivesOnly = (map) => filter(map, x => x > 0);
    return {
        inBoth: positivesOnly(intersection(reactants, products)),
        owedInProducts: positivesOnly(difference(reactants, products)),
        owedInReactants: positivesOnly(difference(products, reactants))
    };
}
export function categorize2(equation) {
    const reactants = sumAll(equation.reactants.map(([coefficient, molecule]) => countElements2(molecule, coefficient)));
    const products = sumAll(equation.products.map(([coefficient, molecule]) => countElements2(molecule, coefficient)));
    //const positivesOnly = <K>(map: Map<K, number>) => filter(map, x => x > 0)
    return {
        inBoth: intersection2(reactants, products),
        owedInProducts: difference2(reactants, products),
        owedInReactants: difference2(products, reactants)
    };
}
// 4-tuple (Bool, Bool, Bool, Bool) = 2^4 = 16 
// 3-tuple (Bool, Bool, Bool) = 2^3 =      8 !== 16 (Int -> (Infinite in 2 directions)) (Natural number -> (Infinite in 1 direction)) string.charAt(Natural)
export const examples = [
    equationWithCoefficient1([
        element('H', 2),
        element('O', 2)
    ], [
        H_2O()
    ]),
    equationWithCoefficient1([
        compound([
            element('C'),
            element('O', 2)
        ]),
        H_2O()
    ], [
        compound([
            element('C', 6),
            element('H', 12),
            element('O', 6)
        ]),
        element('O', 2)
    ]),
    equationWithCoefficient1([
        compound([
            element('H', 2),
            element('S'),
            element('O', 4)
        ]),
        compound([
            element('H'),
            element('I')
        ])
    ], [
        compound([element('H', 2), element('S')]),
        element('I', 2),
        H_2O()
    ])
];
export function randomEquation() {
    return examples[randomIntegerUpTo(examples.length)];
}
function randomIntegerUpTo(max) {
    const between0and1 = Math.random();
    const floatBetween0andMax = between0and1 * max;
    return Math.floor(floatBetween0andMax);
}
// CO2 + H2O → C6H12O6 + O2
// SiCl4 + H2O → H4SiO4 + HCl
// Al + HCl → AlCl3 + H2
// Na2CO3 + HCl → NaCl + H2O + CO2
// C7H6O2 + O2 → CO2 + H2O
// Fe2(SO4)3 + KOH → K2SO4 + Fe(OH)3
// Ca(PO4)2 + SiO2 → P4O10 + CaSiO3
// KClO3 → KClO4 + KCl
// Al2(SO4)3 + Ca(OH)2 → Al(OH)3 + CaSO4
// H2SO4 + HI → H2S + I2 + H2O
export function toTex(molecule) {
    return `\\mathrm{${toString(molecule)}}`;
}
export function toString(molecule) {
    switch (molecule.kind) {
        case "Element":
            return (molecule.subscript === 1) ? `${molecule.element}` : `${molecule.element}_{${molecule.subscript}}`;
        case "Compound":
            return (molecule.subscript === 1) ?
                `${molecule.molecules.map(molecule => toString(molecule)).join('')}` :
                `(${molecule.molecules.map(molecule => toString(molecule)).join('')})_{${molecule.subscript}}`;
    }
}
